import { constants } from '@ali/4ever-cangjie';

const { notEditable } = constants.Selector;

export default function isNotEditable(event: DragEvent) {
  let target = event.target as HTMLElement | null;
  while (
    target &&
    target !== window.document.body
  ) {
    if (target?.getAttribute(notEditable)) return true;
    target = target.parentElement;
  }
  return false;
}
